
import XHeader from 'vux/src/components/x-header/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'

import { validateNotNull, validateInt1 } from 'util/rules'
export default {
data () {
return {
show1: false,
newDate: '',
slimBeauty: {
clientInfoId: 0,
createBy: '',
createTime: '',
getBack: 0,
id: null,
teachDate: '',
teachDish: '',
teachNum: '',
type: '6',
updateBy: '',
updateTime: ''
},
inited: false
}
},
methods: {
saveService () {
if (this.inited) {
this.$vux.toast.text('拼命请求中，请稍候~')
return
}
let data = {
clientInfoId: this.slimBeauty.clientInfoId,
type: 6,
slimBeauty: this.slimBeauty
}
if (validateNotNull(this, this.slimBeauty.teachDate, '教做日期') && validateNotNull(this, this.slimBeauty.teachDish, '教做菜品') && validateInt1(this, this.slimBeauty.teachNum, '教做数量')) {
this.inited = true
this.$tupHttp(this, 'client-biz/client-biz/storeClient/saveService', 'POST', data).then(res => {
this.$vux.toast.text('保存成功！')
setTimeout(() => {
this.$router.push({
path: '/customerSRV',
query: {
id: this.$route.query.id,
type: 6
}
})
}, 500)
}, () => {
this.inited = false
}).catch(() => {
this.inited = false
})
}
},
getTeachDate () {
this.show1 = false
this.slimBeauty.teachDate = this.newDate
},
getTime () {
var _this = this
let yy = new Date().getFullYear()
let mm = new Date().getMonth() + 1 < 10 ? '0' + (new Date().getMonth() + 1) : new Date().getMonth() + 1
let dd = new Date().getDate() < 10 ? '0' + new Date().getDate() : new Date().getDate()
_this.newDate = yy + '-' + mm + '-' + dd
}
},
created () {
this.slimBeauty.clientInfoId = parseInt(this.$route.query.id)
this.getTime()
},
components: {
XHeader,
PopupHeader,
Popup,
Group,
Radio,
Datetime
}
}



